<template>
  <v-content>
    <v-img src="@/assets/img/1.png" class="bdBackground" height="100%" width="100%"></v-img>
    <v-container>
      <v-row justify="center">
        <v-col>
          <v-card>
            <v-card-text>
              <v-card-title>发布商品</v-card-title>
              <v-form v-model="valid" ref="form">
                <v-text-field
                  v-model="good.good_name"
                  :counter="20"
                  :rules="[rules.required,rules.notSpecial]"
                  label="商品名称"
                  prepend-icon="mdi-account"
                  required
                ></v-text-field>
                <v-textarea
                  v-model="good.good_desc"
                  label="商品描述"
                  prepend-icon="mdi-account"
                  :rules="[rules.required]"
                ></v-textarea>
                <v-text-field
                  v-model="good.good_price"
                  :rules="[rules.required,rules.numical]"
                  label="商品价格"
                  prepend-icon="mdi-account"
                  required
                  prefix="￥"
                ></v-text-field>
                <v-input prepend-icon="mdi-account" hide-details>
                  <!-- <cas
                  :data="categories"
                  v-model="casValue"
                  size="large"
                  placeholder="商品分类"
                  @on-change="casChange"
                  />-->
                </v-input>
                <v-file-input
                  multiple
                  label="商品图片"
                  prepend-icon="mdi-camera"
                  v-model="imgFile"
                  counter
                  chips
                  show-size
                  :rules="[rules.imgTypeAndSize]"
                ></v-file-input>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <div class="flex-grow-1"></div>
              <v-btn color="primary" @click="uploadImg">发布</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-snackbar v-model="tip.status" bottom :color="tip.color" :timeout="tip.timeout">{{ tip.msg }}</v-snackbar>
  </v-content>
</template>
<script>
import { log } from "util";
import $qs from "qs";

export default {
  name: "create",
  created() {
    let _this = this;
    this.loadTypeData();
    let user = $qs.parse(sessionStorage.getItem("loginInfo"));
    this.good.user_id = user.sid;
    this.good.user_name = user.nick_name;
  },
  mounted() {
    let _this = this;
  },
  data() {
    return {
      tip: {
        status: false,
        msg: "",
        color: "success",
        timeout: 2000
      },
      casValue: [],
      categories: [],
      imgFile: [],
      valid: "",
      rules: {
        required: value => !!value || "不能为空",
        imgTypeAndSize: v => this.validImgs(),
        notSpecial: v =>
          /^[a-zA-Z0-9\u4e00-\u9fa5]+$/.test(v) || "只能包含字母、数字或中文",
        numical: v => /^[0-9]{1}[0-9]*[.]?[0-9]*$/.test(v) || "价格格式错误"
      },
      good: {
        good_name: "",
        good_desc: "",
        good_price: "",
        good_image_url: "",
        dict_item_code: "",
        user_id: "",
        user_name: ""
      }
    };
  },
  watch: {
    imgFile(newFile, oldFile) {}
  },
  methods: {
    validImgs() {
      if (this.imgFile.length == 0) {
        return "文件不能为空";
      }
      for (var i = 0; i < this.imgFile.length; i++) {
        if (!this.imgFile[i].type.startsWith("image")) {
          return "所有文件必须是PNG、BMP、JPG格式";
        }
        if (this.imgFile[i].size >= 1048576) {
          return "单张图片必须小于1M";
        }
      }
      return true;
    },
    loadTypeData() {
      let _this = this;
      this.$axios({
        method: "get",
        url: "/dict/goods"
      })
        .then(res => {
          _this.categories = res.data;
        })
        .catch(err => {});
    },
    uploadImg() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (this.casValue && this.casValue.length == 0) {
        this.tipShow("请选择商品分类", "error");
        return;
      }
      let _this = this;
      if (this.imgFile.length != 0) {
        let formData = new FormData();
        for (let i = 0; i < this.imgFile.length; i++) {
          formData.append("files", this.imgFile[i]);
        }
        this.$axios({
          method: "post",
          url: "/upload/image",
          data: formData,
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
          .then(res => {
            _this.good.good_image_url = res.data;
            _this.uploadGood();
          })
          .catch(err => {
            _this.tipShow("上传商品图片失败", "error");
          });
      } else {
        this.uploadGood();
      }
    },
    casChange(value, selectedData) {
      this.good.dict_item_code = value[value.length - 1];
    },
    uploadGood() {
      let _this = this;
      this.$axios({
        method: "post",
        url: "/good",
        data: _this.good
      })
        .then(result => {
          _this.tipShow("上架商品成功", "success");
          _this.clearCreate();
        })
        .catch(err => {
          _this.tipShow("上架商品失败", "error");
        });
    },
    tipShow(msg, color) {
      this.tip.status = true;
      this.tip.msg = msg;
      this.tip.color = color ? color : "success";
    },
    clearCreate() {
      this.good.good_desc = "";
      this.good.good_price = "";
      this.good.good_name = "";
      this.casValue = [];
      this.imgFile = [];
    }
  }
};
</script>